.catalog__item {
  position: relative;
}

.catalog__item-container {
  display: grid;
  padding: 20px 0 25px;

  grid-template-columns: 140px 1fr;
}

.catalog__img {
  width: 140px;
  height: 112px;
}

.catalog__title {
  max-width: 80px;
  margin: 0;

  font-size: 16px;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
}

.catalog__descr {
  flex-grow: 1;
  width: 100%;
  max-width: 230px;
  margin-bottom: 20px;

  font-weight: inherit;
  font-size: 12px;
  line-height: 14px;
  font-family: $font-family-other;
  color: $color_dark-gray;
}

.catalog__title-link {
  text-decoration: none;
}

.catalog__table {
  width: 100%;

  border-collapse: collapse;
}

.catalog__table-item {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  font-family: $font-family-other;
  text-align: center;
  color: #444444;
}

.catalog__table-title {
  text-align: left;
}

.catalog__table-value {
  text-align: right;
}

.catalog__item-more h3 {
  max-width: 100%;
  margin-bottom: 7px;
}

.catalog__item-more .catalog__item-container {
  display: block;
  padding: 80px 0 26px;

  text-align: center;

  background-image: url("../img/svg/icon-plus.svg");
  background-repeat: no-repeat;
  background-position: top 25px center;
}

.catalog__btn {
  width: 100%;

  grid-column: 1/-1;
}

.catalog__item-more .catalog__btn {
  color: #444444;

  background-color: #f2f2f2;
}
